.todo-list-template {
  background: white;
  width: 380px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); /* 그림자 */ 
  margin: 0 auto; /* 페이지 중앙 정렬 */
  margin-top: 4rem;
  border-radius: 4px;
  /* border: 1px solid #f1f3f5; */
  border: 1px solid rgba(102, 102, 102, .2);
  background: rgba(228, 218, 228, .2);
}

.title {
  padding: 1rem;
  font-size: 1.2rem;
  letter-spacing: 0.8px;
  text-align: left;
  font-weight: 600;
  color: #666;
  position: relative;
  margin-left: 14.5%;
  /* background: rgba(228, 218, 228, .1); */
  
}

.title > div {
  position: absolute;
  top: 50%;
  left: -5%;
  transform: translateY(-35%);
  width: 22px;
  height: 22px;
  /* border: 1px solid #666; */
  /* background: rgba(228, 218, 228, .5); */
}

.title > div span {
  display: inline-block;
}

.title > div span:first-child {
  border: 1px solid rgba(150, 101, 150, .6);
  width: 14px;
  position: absolute;
  top: 4px;
  left: 0;
}

.title > div span:nth-child(2) {
  border: 1px solid rgba(150, 101, 150, .4);
  width: 9px;
  position: absolute;
  top: 9px;
  left: 0;
}

.title > div span:last-child {
  border: 1px solid rgba(150, 101, 150, .7);
  width: 14px;
  position: absolute;
  top: 14px;
  left: 0;
}

.form-wrapper {
  padding: 1rem;
  border-top: 1px solid rgba(102, 102, 102, .2);
  border-bottom: 1px solid rgba(102, 102, 102, .2);
  /* background: rgba(228, 218, 228, .1); */
}

.todos-wrapper {
  min-height: 5rem;
  /* background: rgba(228, 218, 228, .5); */
}