.form {
  display: flex;
  position: relative;
  /* background: rgba(228, 218, 228, .5); */
  width: 100%;
  height: 100%;
  /* background: rgba(228, 218, 228, .5); */
}

.form input {
  flex: 0.75; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
  margin-left: 1.35rem;
  font-size: 0.8rem;
  color: #999;
  line-height: 1rem;
  padding: 0.5rem;
  height: 1rem;
  outline: none;
  background: rgba(228, 218, 228, .5);
  /* border: 2px solid rgb(224, 221, 221); */
  border: 2px solid #999;
}

.create-button {
  width: 24px;
  height: 23px;
  line-height: 1rem;
  margin-left: 1rem;
  border: 2px solid #999;
  border-radius: 50%;
  color: #999;
  font-size: 1.2rem;
  font-weight: 500;
  cursor: pointer;
  position: absolute;
  right: 5.5%;
  top: 50%;
  transform: translateY(-50%);
}

.create-button span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -67%);
}

.create-button:hover {
  background: #e4e4e4;
  color: rgba(182, 146, 182, .9);
  border-color: rgba(228, 218, 228, .7);

}