.todo-item {
  padding: 1rem 1rem 1rem 0.8rem;
  display: flex;
  align-items: center; /* 세로 가운데 정렬 */
  cursor: pointer;
  transition: all 0.25s;
  user-select: none;
  /* background: rgba(228, 218, 228, .1); */
}

.todo-item:hover {
  /* background: rgba(241, 238, 238, .5); */
  background: rgba(228, 218, 228, .3);
}

/* todo-item 에 마우스가 있을때만 .remove 보이기 */
.todo-item:hover .remove {
  opacity: 1;
}

/* todo-item 사이에 윗 테두리 */
/* .todo-item + .todo-item {
  border-top: 1px solid #f1f3f5;
} */


.remove {
  margin-left: 0.5rem;
  margin-right: 0.85rem;
  color: #a71157;
  font-size: 1rem;
  font-weight: 800;
  opacity: 0;
}

.todo-text {
  flex: 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
  word-break: break-all; /*문자를 강제로 줄바꿈할 수 있음.*/
  font-size: 0.8rem;
  letter-spacing: 1px;
  color: #666;
}

.checked {
  text-decoration: line-through;
  color: #adb5bd;
}

.check-mark {
  font-size: 1.1rem;
  line-height: 1rem;
  margin-left: 0rem;
  margin-right: 28px;
  color: rgb(141, 172, 141);
  font-weight: 800;
}

